.header-container img {
  width: 5.5rem;
}

.modal {
  max-width: 270px;
}
.modal .modal-content {
  max-width: 270px;
}
.modal .modal-content .list-group-item {
  border: none;
  transition: 0.2s;
}
.modal .modal-content .list-group-item:hover {
  filter: brightness(0.95);
}
.modal .modal-content .list-group-item:active {
  color: var(--red);
}